import React, { useState } from 'react';
import './Basic.css';

function Basic({basic}) {
    const [caches, setCaches] = useState([]);
    const [newlist, setNewlist] = useState("")

    function equal(){
        const theInput = document.getElementById("theInput");

        if (theInput.value.length > 0) {
            
            let numbers = newlist.split(/[\u00F7\u00D7%+^-]/g).filter(e=>e!=="")
            let operators = newlist.split(/[0-9]|[.]/g).filter(e=>e!=="")
            let equateList = []
       
            console.log(theInput.value[0])
            // if(!theInput.value[0]==="-"){

            if(!theInput.value[0].match(/[%^+\u00F7\u00D7-]/g)){
                for (var i = 0; i < numbers.length; i++){
                    equateList.push(numbers[i])
                    equateList.push(operators[i])
                }}
            else{
                for (var j = 0; j < operators.length; j++){
                    equateList.push(operators[j])
                    equateList.push(numbers[j])
                }
            }
            
            let filteredList = equateList.filter(e => e !== undefined && e !== "")
            let filteredList2 = equateList.filter(e => e !== undefined && e !== "")
            console.log(filteredList)
            
            try {
            for (var k = 0; k < filteredList.length; k++){
                if (k === 0 && filteredList[0] === "-") {
                    filteredList[1] = filteredList[0] + filteredList[1]
                    filteredList[0] = ""
                    filteredList = filteredList.filter(e=>e!=="")
                }
                else if (k === 0 && filteredList[0].match(/[%^+\u00F7\u00D7]/g)) {
                    filteredList[0] = ""
                    filteredList = filteredList.filter(e=>e!=="")
                }
                if (filteredList[k].length>1 && filteredList[k].includes("-") && !filteredList[k].match(/[0-9]/g)) {
                    filteredList[k+1] = filteredList[k][filteredList[k].indexOf("-")] + filteredList[k+1]
                    filteredList[k] = filteredList[k][0]
                }
                // if (filteredList[k].length>1 && !filteredList[k].match(/[0-9]/g)) {
                //     filteredList[k] = filteredList[k][0]
                // }
                }
            }
            catch (TypeError){
                
            }

            let flnum = filteredList.filter(e => e.match(/[0-9]/i)).map(e => +e)
            let flop = filteredList.filter(e=> !e.match(/[0-9]/i))

            if (theInput.value.includes("^")) {
                while (flop.includes("^")) {
                    const index = flop.indexOf("^")
                    flnum[index] = flnum[index] ** flnum[index+1]
                    flnum[index+1] = ""
                    flop[index] = ""
                }
                flop = flop.filter(function(e){ return e!==""})
                flnum = flnum.filter(function(e){ return e!==""})
            }
            
            var equalize = flnum.toString().length >= 1 ? flnum[0] : parseInt(flnum);
            for (var l = 0; l <= flop.length; l++) {
                if(flop[l]==="+") equalize += flnum[l+1];
                if(flop[l]==="-") equalize -= flnum[l+1];
                if(flop[l]==="\u00D7") equalize *= flnum[l+1];
                if(flop[l]==="\u00F7") equalize /= flnum[l+1];
                if (flop[l] === "%") equalize *= 0.01;
            }
            setCaches([...caches,
                {
                    input: !isNaN(equalize) ? filteredList.join(""):filteredList2.join(""),
                    sumnumber: !isNaN(equalize) ? (parseFloat(equalize.toFixed(4))) : "Syntax Error",
                }
            ])
            // setNewlist(!isNaN(equalize) ? equalize : "Syntax Error");
            setNewlist("");
        }
    }
    const enter = (e) => {
        switch (e.keyCode) {
            case 191: e.target.value += "\u00F7"; break
            case 56: if(e.shiftKey) e.target.value += "\u00D7"; break
            case 13: equal(); break
            default: break
        }
    }
    const addtoInput = (e) => {
        const theInput = document.getElementById("theInput");
        theInput.value += e.target.value;
        setNewlist(theInput.value);
    } 

    const clear = (e) => {
        e.preventDefault();
        setNewlist("")
    } 

    const OnlyNumericalValue = (e) => {
        e.target.value = e.target.value.replace(/[^0-9%^.+\u00F7\u00D7-]/g, '')
    }
    const inputHandlre = (e) => {
        setNewlist(e.target.value);
    }

    return (
        <div className="basic__container" style={{display: basic ? "" : "none"}}>
            <div className="conter" >
            {
            caches.map((cache, i) => (
                <div key={i} className="conterInner">
                    <h1 >{cache.input}</h1> 
                    <h1 >=</h1> 
                    <h1 >{cache.sumnumber}</h1>
                </div>
            ))
            }
            </div>
            <input type="text" id="theInput"
            value={newlist}
                onChange={inputHandlre}
                onKeyDown={enter}
                onInput={OnlyNumericalValue}
                autoComplete="off"
            />

            <div className="buttons">
            <button value="0" className="n0" onMouseUp={addtoInput}>0</button>
            <button value="1" className="n1" onMouseUp={addtoInput}>1</button>
            <button value="2" className="n2" onMouseUp={addtoInput}>2</button>
            <button value="3" className="n3" onMouseUp={addtoInput}>3</button>
            <button value="4" className="n4" onMouseUp={addtoInput}>4</button>
            <button value="5" className="n5" onMouseUp={addtoInput}>5</button>
            <button value="6" className="n6" onMouseUp={addtoInput}>6</button>
            <button value="7" className="n7" onMouseUp={addtoInput}>7</button>
            <button value="8" className="n8" onMouseUp={addtoInput}>8</button>
            <button value="9" className="n9" onMouseUp={addtoInput}>9</button>    
            
            <button value="." className="period" onMouseUp={addtoInput}>.</button>
            <button value="%" className="percent" onMouseUp={addtoInput}>%</button>
            <button value={"\u00F7"} className="divide" onMouseUp={addtoInput}>/</button>
            <button value={"\u00D7"} className="multiply" onMouseUp={addtoInput}>x</button>
            <button value="-"className="minus" onMouseUp={addtoInput}>-</button>
            <button value="+" className="addition" onMouseUp={addtoInput}>+</button>
            <button className="equal" onMouseUp={equal}>=</button>
            <button className="clear" onMouseUp={clear}>C</button>
            </div>
        </div>
    )
}

export default Basic
